.proj_container_section {
    width: 90%;
    padding: 20px 30px;
    display: flex;
};
  
  .proj_container_section,
  .proj_left,
  .proj_right {
    display: flex;
    align-content: center;
    justify-content: center;
};
  .proj_left,
  .proj_right {
    flex-wrap: wrap;
    width: 50%;
};
  .proj_left {
    text-align: left;
    gap: 12px;
};
  .proj_left h3 {
    font-size: 36px;
    flex-wrap: wrap;
    display: flex;
};
  .proj_left p {
    line-height: 30px;
    font-size: 18px;
    width: 90%;
};
  .proj_left h3,
  .proj_left p,
  .bot_btn {
    width: 90%;
    color: #000;
};
  .proj_right img {
    height: 550px;
};
  
  @media screen and (min-width: 1024px) {
    .proj_left h3 {
      color: #f5b1ff;
      padding-bottom: 3px;
      text-shadow: 0px 0px 3px #20b8ff7a;
    }
    .proj_left p {
      color: #9f9f9f;
    }
    .proj_container_section {
      display: flex;
    }
};
  
  @media screen and (max-width: 920px) {
    .proj_container_section {
      flex-direction: column;
      width: 100%;
      align-items: center;
      justify-content: center;
    }
    .proj_left,
    .proj_right {
      width: 90%;
    }
    .bot_btn {
      margin: 0;
    }
    .proj_left h3,
    .proj_left p,
    .bot_btn {
      width: 100%;
    }
    .proj_left p {
      text-align: left;
    }
};
  @media screen and (max-width: 720px) {
    .proj_container_section {
      flex-direction: column;
    }
    .proj_right img {
      width: 100%;
      height: auto;
    }
};
  
  @media screen and (max-width: 420px) {
    .proj_left h3 {
      font-size: 30px;
      text-align: left;
      padding-top: 18px;
    }
    .proj_container_section {
      padding: 20px 18px;
    }
};
  