.bot_btn {
    margin-top: 20px;
  }
  .bot_git_btn {
    box-shadow: 0px 0px 13px #55198b;
    color: #fff;
    font-weight: 500;
    line-height: 1.1;
    width: 230px;
    padding: 13px 22px;
    margin-right: 0;
    border-radius: 6px;
    text-align: center;
    text-decoration: none;
    font-family: Google Sans Regular;
    display: block;
    margin-top: 20px;
    font-size: 18px;
    cursor: pointer;
    letter-spacing: 2px;
  }
  .bot_git_btn:active {
    transform: scale(0.9);
  }
  .bot_git_btn:hover {
    background: #9c27b0;
    box-shadow: 0px 0px 13px #251335;
  }
  
  @media screen and (min-width: 1024px) {
    .bot_git_btn {
      box-shadow: #482e87 0px 0px 3px;
    }
  }
  